body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.container {
  min-width: 600px;
  min-height: 250px;
  background-color: #f1e7dd;
  border-radius: 10px;
  flex-direction: column;
  margin: 10px;
  padding: 30px;
  display: flex;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2), 0 3px 10px rgba(0, 0, 0, .19);
}

.container:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
  text-align: left;
}

.container:-webkit-any(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: right;
}

.container:is(:lang(ae), :lang(ar), :lang(arc), :lang(bcc), :lang(bqi), :lang(ckb), :lang(dv), :lang(fa), :lang(glk), :lang(he), :lang(ku), :lang(mzn), :lang(nqo), :lang(pnb), :lang(ps), :lang(sd), :lang(ug), :lang(ur), :lang(yi)) {
  text-align: right;
}

.imagesContainer {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.header {
  align-items: top;
  justify-content: space-between;
  display: flex;
}

.userName {
  justify-content: start;
  margin-bottom: 10px;
  margin-right: 0;
  display: flex;
}

.userNameContent {
  font-weight: 600px;
  text-align: center;
  margin: 10px;
  font-size: 20px;
}

.title {
  font-weight: 700px;
  font-size: 28px;
}

.rightContainer {
  text-align: right;
}

.details {
  margin: 10px;
  padding: 10px;
}

.content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.leftContainer {
  font-weight: 500px;
  font-size: 18px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: 20s linear infinite App-logo-spin;
    animation: 20s linear infinite App-logo-spin;
  }
}

.App-header {
  min-height: 100vh;
  color: #fff;
  background-color: #282c34;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: calc(10px + 2vmin);
  display: flex;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }

  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

